import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Label } from '../components/ui/label';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '../components/ui/card';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from '../components/ui/dialog';
import { toast } from 'sonner';
import api from '../services/api';


const JournalPage = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [loading, setLoading] = useState(false);
  const [entries, setEntries] = useState([]);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  
  // Delete confirmation
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [entryToDelete, setEntryToDelete] = useState(null);

  useEffect(() => {
    if (user?.role !== 'admin') {
      toast.error('  ');
      navigate('/dashboard');
      return;
    }
    fetchEntries();
  }, [user, navigate]);

  const fetchEntries = async () => {
    setLoading(true);
    try {
      const params = {};
      if (startDate) params.start_date = startDate;
      if (endDate) params.end_date = endDate;
      
      const response = await api.get('/accounting/journal-entries', { params });
      setEntries(response.data.entries || []);
      toast.success('   ');
    } catch (error) {
      console.error('Error fetching entries:', error);
      toast.error('   ');
    }
    setLoading(false);
  };

  const handleDeleteClick = (entry) => {
    setEntryToDelete(entry);
    setShowDeleteDialog(true);
  };

  const handleDeleteConfirm = async () => {
    if (!entryToDelete) return;

    try {
      await api.delete('/accounting/journal-entries/${entryToDelete.id}');
      toast.success('   ');
      setShowDeleteDialog(false);
      setEntryToDelete(null);
      fetchEntries();
    } catch (error) {
      console.error('Error deleting entry:', error);
      const errorMsg = error.response?.data?.detail || '   ';
      toast.error(errorMsg);
    }
  };

  const formatCurrency = (amount) => {
    return amount.toLocaleString();
  };

  return (
    <div className="min-h-screen bg-[#F5F7FA]">
      
      <div className="container mx-auto p-4 sm:p-6 space-y-6">
        {/* Header */}
        <Card className="border-0 shadow-lg">
          <CardHeader className="bg-gradient-to-l from-blue-50 to-blue-100">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
              <div>
                <CardTitle className="text-2xl sm:text-3xl">  </CardTitle>
                <CardDescription className="text-base">
                     
                </CardDescription>
              </div>
              <Button onClick={() => navigate('/manual-journal-entry')}>
                  
              </Button>
            </div>
          </CardHeader>
        </Card>

        {/* Filters */}
        <Card>
          <CardContent className="pt-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="space-y-2">
                <Label> </Label>
                <Input
                  type="date"
                  value={startDate}
                  onChange={(e) => setStartDate(e.target.value)}
                />
              </div>

              <div className="space-y-2">
                <Label> </Label>
                <Input
                  type="date"
                  value={endDate}
                  onChange={(e) => setEndDate(e.target.value)}
                />
              </div>

              <div className="space-y-2 flex items-end">
                <Button onClick={fetchEntries} disabled={loading} className="w-full">
                  {loading ? ' ...' : ' '}
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Entries List */}
        <div className="space-y-4">
          {loading ? (
            <Card>
              <CardContent className="p-8 text-center"> ...</CardContent>
            </Card>
          ) : entries.length === 0 ? (
            <Card>
              <CardContent className="p-8 text-center text-muted-foreground">
                   
              </CardContent>
            </Card>
          ) : (
            entries.map((entry) => (
              <Card key={entry.id} className="border-2 hover:shadow-lg transition-shadow">
                <CardHeader className="bg-gradient-to-l from-gray-50 to-white">
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="text-xl">
                         : {entry.entry_number}
                      </CardTitle>
                      <CardDescription>
                        {new Date(entry.date).toLocaleDateString('ar-IQ')} - {entry.description}
                      </CardDescription>
                    </div>
                    <div className="flex gap-2">
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => handleDeleteClick(entry)}
                      >
                         
                      </Button>
                    </div>
                  </div>
                </CardHeader>
                <CardContent className="pt-6">
                  <div className="overflow-x-auto">
                    <table className="w-full text-sm">
                      <thead className="bg-gray-100">
                        <tr>
                          <th className="p-2 text-right"> </th>
                          <th className="p-2 text-right"> </th>
                          <th className="p-2 text-center"></th>
                          <th className="p-2 text-center"></th>
                        </tr>
                      </thead>
                      <tbody>
                        {entry.lines?.map((line, idx) => (
                          <tr key={idx} className="border-t">
                            <td className="p-2">{line.account_code}</td>
                            <td className="p-2">{line.account_name || '-'}</td>
                            <td className="p-2 text-center font-bold text-blue-700">
                              {line.debit > 0 ? formatCurrency(line.debit) : '-'}
                            </td>
                            <td className="p-2 text-center font-bold text-green-700">
                              {line.credit > 0 ? formatCurrency(line.credit) : '-'}
                            </td>
                          </tr>
                        ))}
                        <tr className="border-t-2 bg-gray-50 font-bold">
                          <td className="p-2" colSpan="2"></td>
                          <td className="p-2 text-center text-blue-700">
                            {formatCurrency(entry.total_debit)}
                          </td>
                          <td className="p-2 text-center text-green-700">
                            {formatCurrency(entry.total_credit)}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </CardContent>
              </Card>
            ))
          )}
        </div>

        {/* Delete Confirmation Dialog */}
        <Dialog open={showDeleteDialog} onOpenChange={setShowDeleteDialog}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>  </DialogTitle>
              <DialogDescription>
                      
              </DialogDescription>
            </DialogHeader>

            {entryToDelete && (
              <div className="py-4 space-y-2">
                <p><strong> :</strong> {entryToDelete.entry_number}</p>
                <p><strong>:</strong> {entryToDelete.description}</p>
                <p><strong>:</strong> {formatCurrency(entryToDelete.total_debit)}</p>
                <p className="text-red-600 text-sm">
                            
                </p>
              </div>
            )}

            <DialogFooter>
              <Button variant="outline" onClick={() => setShowDeleteDialog(false)}>
                
              </Button>
              <Button variant="destructive" onClick={handleDeleteConfirm}>
                  
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  );
};

export default JournalPage;
