import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card';
import { Button } from '../components/ui/button';
import { toast } from 'sonner';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from '../components/ui/dialog';
import api from '../services/api';

const API = process.env.REACT_APP_BACKEND_URL || 'http://localhost:8001';

const CurrencyRevaluationPage = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  
  const [accounts, setAccounts] = useState([]);
  const [currentRate, setCurrentRate] = useState(1300);
  const [revaluations, setRevaluations] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showPreview, setShowPreview] = useState(false);
  const [previewData, setPreviewData] = useState(null);
  
  const [formData, setFormData] = useState({
    account_code: '',
    amount: '',
    currency: 'IQD',
    exchange_rate: '',
    operation_type: 'debit',
    direction: 'iqd_to_usd',
    notes: ''
  });

  useEffect(() => {
    if (user?.role !== 'admin') {
      toast.error('  ');
      navigate('/dashboard');
      return;
    }
    fetchAccounts();
    fetchCurrentRate();
    fetchRevaluations();
  }, [user, navigate]);

  const fetchAccounts = async () => {
    try {
      const token = localStorage.getItem('token');
      
      // Fetch from chart of accounts (same as used in journal/ledger)
      const response = await api.get('/api/accounting/accounts', {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      // Handle response - accounts might be in response.data.accounts or response.data
      let accountsList = [];
      if (Array.isArray(response.data)) {
        accountsList = response.data;
      } else if (response.data.accounts && Array.isArray(response.data.accounts)) {
        accountsList = response.data.accounts;
      }
      
      // Filter only active accounts
      const activeAccounts = accountsList.filter(acc => acc.is_active !== false);
      
      setAccounts(activeAccounts);
      
      if (activeAccounts.length === 0) {
        toast.error('     .   " " .');
      }
    } catch (error) {
      console.error('Error fetching accounts:', error);
      setAccounts([]);
      toast.error('      ');
    }
  };
  
  const syncAgentsToChart = async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('token');
      const response = await api.post('/api/sync-agents-to-chart', {}, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      toast.success(`  ! ${response.data.total} `);
      
      // Refresh accounts
      fetchAccounts();
    } catch (error) {
      console.error('Error syncing:', error);
      toast.error('   ');
    } finally {
      setLoading(false);
    }
  };

  const fetchCurrentRate = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await api.get('/api/exchange-rates/current', {
        headers: { Authorization: `Bearer ${token}` }
      });
      setCurrentRate(response.data.rate);
      setFormData(prev => ({ ...prev, exchange_rate: response.data.rate }));
    } catch (error) {
      console.error('Error fetching rate:', error);
    }
  };

  const fetchRevaluations = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await api.get('/api/currency-revaluations?limit=50', {
        headers: { Authorization: `Bearer ${token}` }
      });
      setRevaluations(response.data);
    } catch (error) {
      console.error('Error fetching revaluations:', error);
    }
  };

  const calculateEquivalent = () => {
    const amount = parseFloat(formData.amount) || 0;
    const rate = parseFloat(formData.exchange_rate) || 1;
    
    if (formData.direction === 'iqd_to_usd') {
      return (amount / rate).toFixed(2);
    } else {
      return (amount * rate).toFixed(2);
    }
  };

  const handlePreview = () => {
    if (!formData.account_code) {
      toast.error('  ');
      return;
    }
    if (!formData.amount || parseFloat(formData.amount) <= 0) {
      toast.error('   ');
      return;
    }
    if (!formData.exchange_rate || parseFloat(formData.exchange_rate) <= 0) {
      toast.error('    ');
      return;
    }

    const account = accounts.find(a => a.code === formData.account_code);
    const equivalent = calculateEquivalent();

    setPreviewData({
      account_name: account?.name || '',
      amount: parseFloat(formData.amount),
      currency: formData.currency,
      exchange_rate: parseFloat(formData.exchange_rate),
      equivalent_amount: parseFloat(equivalent),
      equivalent_currency: formData.direction === 'iqd_to_usd' ? 'USD' : 'IQD',
      operation_type: formData.operation_type,
      direction: formData.direction,
      notes: formData.notes
    });

    setShowPreview(true);
  };

  const handleSubmit = async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('token');
      await api.post('/api/currency-revaluation', {
        account_code: formData.account_code,
        amount: parseFloat(formData.amount),
        currency: formData.currency,
        exchange_rate: parseFloat(formData.exchange_rate),
        operation_type: formData.operation_type,
        direction: formData.direction,
        notes: formData.notes || undefined
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });

      toast.success('   ');
      setShowPreview(false);
      
      // Reset form
      setFormData({
        account_code: '',
        amount: '',
        currency: 'IQD',
        exchange_rate: currentRate,
        operation_type: 'debit',
        direction: 'iqd_to_usd',
        notes: ''
      });

      fetchRevaluations();
      fetchAccounts(); // Refresh to show updated balances
    } catch (error) {
      console.error('Error creating revaluation:', error);
      toast.error(error.response?.data?.detail || '    ');
    } finally {
      setLoading(false);
    }
  };

  const getDirectionLabel = (direction) => {
    return direction === 'iqd_to_usd' ? '   ' : '   ';
  };

  const getOperationTypeLabel = (type) => {
    return type === 'debit' ? '' : '';
  };

  return (
    <div className="min-h-screen bg-background">
      
      <div className="container mx-auto p-4 sm:p-6">
        <div className="mb-6">
          <div className="flex justify-between items-center">
            <div>
              <h1 className="text-2xl sm:text-3xl font-bold text-primary mb-2">   </h1>
              <p className="text-muted-foreground">     </p>
            </div>
            <Button
              onClick={syncAgentsToChart}
              disabled={loading}
              variant="outline"
              className="bg-blue-50 hover:bg-blue-100 border-blue-300"
            >
                
            </Button>
          </div>
        </div>

        {/* Form Card */}
        <Card className="mb-6">
          <CardHeader className="bg-primary/5">
            <CardTitle>  </CardTitle>
            <CardDescription>    </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4 mt-4">
            {/* Account Selection */}
            <div>
              <label className="block text-sm font-medium mb-2">  </label>
              <select
                value={formData.account_code}
                onChange={(e) => setFormData({ ...formData, account_code: e.target.value })}
                className="w-full p-3 border rounded-lg text-base"
              >
                <option value="">    ...</option>
                {accounts.map((account) => (
                  <option key={account.code} value={account.code}>
                    {account.name} - {account.code}
                  </option>
                ))}
              </select>
              {formData.account_code && (
                <p className="text-xs text-gray-500 mt-1">
                   : {accounts.find(a => a.code === formData.account_code)?.name} - {formData.account_code}
                </p>
              )}
            </div>

            {/* Show Selected Account Balance */}
            {formData.account_code && (
              <div className="bg-blue-50 border border-blue-200 rounded-lg p-3">
                {(() => {
                  const account = accounts.find(a => a.code === formData.account_code);
                  return account ? (
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <p className="text-sm text-gray-600"> :</p>
                        <p className="font-bold text-lg">{(account.balance_iqd || 0).toLocaleString()} IQD</p>
                      </div>
                      <div>
                        <p className="text-sm text-gray-600"> :</p>
                        <p className="font-bold text-lg">{(account.balance_usd || 0).toLocaleString()} USD</p>
                      </div>
                    </div>
                  ) : null;
                })()}
              </div>
            )}

            {/* Direction */}
            <div>
              <label className="block text-sm font-medium mb-2">  </label>
              <select
                value={formData.direction}
                onChange={(e) => setFormData({ ...formData, direction: e.target.value })}
                className="w-full p-3 border rounded-lg"
              >
                <option value="iqd_to_usd">    ( )</option>
                <option value="usd_to_iqd">    ( )</option>
              </select>
            </div>

            {/* Amount */}
            <div>
              <label className="block text-sm font-medium mb-2">
                  ({formData.direction === 'iqd_to_usd' ? '' : ''})
              </label>
              <input
                type="number"
                step="0.01"
                value={formData.amount}
                onChange={(e) => setFormData({ ...formData, amount: e.target.value })}
                className="w-full p-3 border rounded-lg"
                placeholder=" ..."
              />
            </div>

            {/* Exchange Rate */}
            <div>
              <label className="block text-sm font-medium mb-2">  </label>
              <input
                type="number"
                step="0.01"
                value={formData.exchange_rate}
                onChange={(e) => setFormData({ ...formData, exchange_rate: e.target.value })}
                className="w-full p-3 border rounded-lg"
                placeholder=" ..."
              />
              <p className="text-xs text-gray-500 mt-1"> : {currentRate.toLocaleString()}</p>
            </div>

            {/* Equivalent Amount Display */}
            {formData.amount && formData.exchange_rate && (
              <div className="bg-green-50 border-2 border-green-300 rounded-lg p-4">
                <p className="text-sm text-gray-600 mb-1">  :</p>
                <p className="font-bold text-2xl text-green-700">
                  {calculateEquivalent()} {formData.direction === 'iqd_to_usd' ? 'USD' : 'IQD'}
                </p>
              </div>
            )}

            {/* Operation Type */}
            <div>
              <label className="block text-sm font-medium mb-2">  </label>
              <div className="flex gap-4">
                <label className="flex items-center gap-2 cursor-pointer">
                  <input
                    type="radio"
                    name="operation_type"
                    value="debit"
                    checked={formData.operation_type === 'debit'}
                    onChange={(e) => setFormData({ ...formData, operation_type: e.target.value })}
                  />
                  <span> (Debit) -   </span>
                </label>
                <label className="flex items-center gap-2 cursor-pointer">
                  <input
                    type="radio"
                    name="operation_type"
                    value="credit"
                    checked={formData.operation_type === 'credit'}
                    onChange={(e) => setFormData({ ...formData, operation_type: e.target.value })}
                  />
                  <span> (Credit) -  </span>
                </label>
              </div>
            </div>

            {/* Notes */}
            <div>
              <label className="block text-sm font-medium mb-2">  ()</label>
              <textarea
                value={formData.notes}
                onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                className="w-full p-3 border rounded-lg"
                rows="2"
                placeholder="  ..."
              />
            </div>

            {/* Submit Button */}
            <Button
              onClick={handlePreview}
              className="w-full h-12 text-lg font-bold"
              disabled={loading}
            >
                
            </Button>
          </CardContent>
        </Card>

        {/* Revaluations History */}
        <Card>
          <CardHeader className="bg-primary/5">
            <CardTitle>  </CardTitle>
            <CardDescription> 50 </CardDescription>
          </CardHeader>
          <CardContent className="mt-4">
            {revaluations.length === 0 ? (
              <p className="text-center text-gray-500 py-8">   </p>
            ) : (
              <div className="space-y-3">
                {revaluations.map((rev) => (
                  <div
                    key={rev.id}
                    className="border rounded-lg p-4 hover:bg-accent/50 transition-colors"
                  >
                    <div className="flex justify-between items-start mb-2">
                      <div>
                        <p className="font-bold text-lg">{rev.account_name}</p>
                        <p className="text-sm text-gray-600">{rev.account_code}</p>
                      </div>
                      <span className={`px-3 py-1 rounded-full text-sm font-semibold ${
                        rev.operation_type === 'debit' ? 'bg-red-100 text-red-700' : 'bg-green-100 text-green-700'
                      }`}>
                        {getOperationTypeLabel(rev.operation_type)}
                      </span>
                    </div>
                    
                    <div className="grid grid-cols-2 gap-3 mb-2">
                      <div className="bg-blue-50 p-2 rounded">
                        <p className="text-xs text-gray-600"> </p>
                        <p className="font-bold">{rev.amount.toLocaleString()} {rev.currency}</p>
                      </div>
                      <div className="bg-purple-50 p-2 rounded">
                        <p className="text-xs text-gray-600"> </p>
                        <p className="font-bold">
                          {rev.equivalent_amount.toLocaleString()} {rev.currency === 'IQD' ? 'USD' : 'IQD'}
                        </p>
                      </div>
                    </div>

                    <div className="flex justify-between items-center text-sm text-gray-600">
                      <span> {getDirectionLabel(rev.direction)}</span>
                      <span> {rev.exchange_rate.toLocaleString()}</span>
                    </div>

                    {rev.notes && (
                      <p className="text-sm text-gray-600 mt-2 bg-gray-50 p-2 rounded">
                         {rev.notes}
                      </p>
                    )}

                    <div className="flex justify-between items-center mt-2 text-xs text-gray-500">
                      <span>: {rev.created_by}</span>
                      <span>{new Date(rev.created_at).toLocaleString('ar-IQ')}</span>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Preview Dialog */}
        <Dialog open={showPreview} onOpenChange={setShowPreview}>
          <DialogContent className="sm:max-w-[600px]">
            <DialogHeader>
              <DialogTitle className="text-2xl text-center">   </DialogTitle>
              <DialogDescription className="text-center">
                    
              </DialogDescription>
            </DialogHeader>

            {previewData && (
              <div className="space-y-4">
                {/* Account Info */}
                <div className="bg-blue-50 border-2 border-blue-200 rounded-lg p-4">
                  <p className="text-sm text-gray-600">:</p>
                  <p className="font-bold text-lg">{previewData.account_name}</p>
                </div>

                {/* Operation Details */}
                <div className="grid grid-cols-2 gap-3">
                  <div className="bg-gray-50 p-3 rounded">
                    <p className="text-xs text-gray-600 mb-1"> :</p>
                    <p className="font-semibold">{getOperationTypeLabel(previewData.operation_type)}</p>
                  </div>
                  <div className="bg-gray-50 p-3 rounded">
                    <p className="text-xs text-gray-600 mb-1">:</p>
                    <p className="font-semibold">{getDirectionLabel(previewData.direction)}</p>
                  </div>
                </div>

                {/* Journal Entry Preview */}
                <div className="border-2 border-green-300 rounded-lg p-4 bg-green-50">
                  <p className="font-bold text-center mb-3 text-green-800">  </p>
                  
                  <div className="space-y-2">
                    {/* Debit Entry */}
                    <div className="bg-white border border-red-300 rounded p-3">
                      <div className="flex justify-between items-center">
                        <span className="font-semibold text-red-700"> ():</span>
                        <span className="font-bold text-lg">
                          {previewData.operation_type === 'debit'
                            ? `${previewData.amount.toLocaleString()} ${previewData.currency}`
                            : `${previewData.equivalent_amount.toLocaleString()} ${previewData.equivalent_currency}`
                          }
                        </span>
                      </div>
                    </div>

                    {/* Credit Entry */}
                    <div className="bg-white border border-green-300 rounded p-3">
                      <div className="flex justify-between items-center">
                        <span className="font-semibold text-green-700"> ():</span>
                        <span className="font-bold text-lg">
                          {previewData.operation_type === 'credit'
                            ? `${previewData.amount.toLocaleString()} ${previewData.currency}`
                            : `${previewData.equivalent_amount.toLocaleString()} ${previewData.equivalent_currency}`
                          }
                        </span>
                      </div>
                    </div>
                  </div>

                  <div className="mt-3 text-center text-sm text-gray-600">
                      : {previewData.exchange_rate.toLocaleString()}
                  </div>
                </div>

                {/* Notes */}
                {previewData.notes && (
                  <div className="bg-yellow-50 border border-yellow-200 rounded p-3">
                    <p className="text-sm text-gray-600"> :</p>
                    <p className="font-semibold">{previewData.notes}</p>
                  </div>
                )}

                {/* Warning */}
                <div className="bg-orange-50 border border-orange-300 rounded p-3 text-center">
                  <p className="text-sm text-orange-800">
                           
                  </p>
                </div>
              </div>
            )}

            <DialogFooter className="flex gap-3">
              <Button
                onClick={handleSubmit}
                disabled={loading}
                className="flex-1 bg-green-600 hover:bg-green-700 h-12 text-base font-bold"
              >
                {loading ? '  ...' : '  '}
              </Button>
              <Button
                onClick={() => setShowPreview(false)}
                variant="outline"
                className="flex-1 h-12 text-base"
                disabled={loading}
              >
                 
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  );
};

export default CurrencyRevaluationPage;
