import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Label } from '../components/ui/label';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '../components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../components/ui/select';
import { toast } from 'sonner';
import api from '../services/api';


const ReportsPage = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [loading, setLoading] = useState(false);
  
  // Report settings
  const [reportType, setReportType] = useState('daily'); // daily, monthly, yearly
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);
  
  // Report data
  const [commissionsReport, setCommissionsReport] = useState(null);
  const [agentsReport, setAgentsReport] = useState(null);
  
  // Tab state
  const [activeTab, setActiveTab] = useState('summary'); // summary, agents

  useEffect(() => {
    if (user?.role !== 'admin') {
      toast.error('  ');
      navigate('/dashboard');
      return;
    }
  }, [user, navigate]);

  const fetchReports = async () => {
    setLoading(true);
    try {
      // Get date parameter based on report type
      let dateParam = selectedDate;
      if (reportType === 'monthly') {
        dateParam = selectedDate.substring(0, 7); // YYYY-MM
      } else if (reportType === 'yearly') {
        dateParam = selectedDate.substring(0, 4); // YYYY
      }
      
      // Fetch commissions report
      const commissionsRes = await api.get('/reports/commissions', {
        params: { report_type: reportType, date: dateParam }
      });
      setCommissionsReport(commissionsRes.data);
      
      // Fetch agents profit report
      const agentsRes = await api.get('/reports/agents-profit', {
        params: { report_type: reportType, date: dateParam }
      });
      setAgentsReport(agentsRes.data);
      
      toast.success('   ');
    } catch (error) {
      console.error('Error fetching reports:', error);
      toast.error('   ');
    }
    setLoading(false);
  };

  const getDateInputType = () => {
    if (reportType === 'daily') return 'date';
    if (reportType === 'monthly') return 'month';
    return 'text'; // For yearly, we'll use a number input
  };

  const formatCurrency = (amount, currency = 'IQD') => {
    return `${amount.toLocaleString()} ${currency}`;
  };

  return (
    <div className="min-h-screen bg-[#F5F7FA]">
      
      <div className="container mx-auto p-4 sm:p-6 space-y-6">
        {/* Header */}
        <Card className="border-0 shadow-lg">
          <CardHeader className="bg-gradient-to-l from-green-50 to-green-100">
            <CardTitle className="text-2xl sm:text-3xl">  </CardTitle>
            <CardDescription className="text-base">
                 
            </CardDescription>
          </CardHeader>
        </Card>

        {/* Report Settings */}
        <Card>
          <CardHeader>
            <CardTitle> </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="space-y-2">
                <Label> </Label>
                <Select value={reportType} onValueChange={setReportType}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="daily"> </SelectItem>
                    <SelectItem value="monthly"> </SelectItem>
                    <SelectItem value="yearly"> </SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label></Label>
                {reportType === 'yearly' ? (
                  <Input
                    type="number"
                    value={selectedDate.substring(0, 4)}
                    onChange={(e) => setSelectedDate(`${e.target.value}-01-01`)}
                    placeholder="2024"
                    min="2020"
                    max="2030"
                  />
                ) : (
                  <Input
                    type={getDateInputType()}
                    value={reportType === 'monthly' ? selectedDate.substring(0, 7) : selectedDate}
                    onChange={(e) => {
                      if (reportType === 'monthly') {
                        setSelectedDate(`${e.target.value}-01`);
                      } else {
                        setSelectedDate(e.target.value);
                      }
                    }}
                  />
                )}
              </div>

              <div className="space-y-2">
                <Button 
                  onClick={fetchReports} 
                  disabled={loading}
                  className="flex-1"
                >
                  {loading ? ' ...' : '  '}
                </Button>
                
                {commissionsReport && agentsReport && (
                  <PrintButton
                    componentToPrint={
                      <AccountingReport
                        title="  "
                        subtitle={` ${reportType === 'daily' ? '' : reportType === 'monthly' ? '' : ''}`}
                        dateRange={selectedDate}
                        summary={[
                          { 
                            label: '   (IQD)', 
                            value: `${commissionsReport.earned?.iqd?.toLocaleString() || 0} IQD`,
                            color: '#d1fae5',
                            borderColor: '#10b981',
                            textColor: '#059669'
                          },
                          { 
                            label: '   (USD)', 
                            value: `${commissionsReport.earned?.usd?.toLocaleString() || 0} USD`,
                            color: '#d1fae5',
                            borderColor: '#10b981',
                            textColor: '#059669'
                          },
                          { 
                            label: '   (IQD)', 
                            value: `${commissionsReport.paid?.iqd?.toLocaleString() || 0} IQD`,
                            color: '#fee2e2',
                            borderColor: '#ef4444',
                            textColor: '#991b1b'
                          },
                          { 
                            label: '  (IQD)', 
                            value: `${commissionsReport.net?.iqd?.toLocaleString() || 0} IQD`,
                            color: '#dbeafe',
                            borderColor: '#3b82f6',
                            textColor: '#1e40af'
                          }
                        ]}
                        data={agentsReport}
                        columns={[
                          { header: ' ', field: 'display_name', bold: true },
                          { header: '  ', field: 'outgoing_count', align: 'center' },
                          { header: '  ', field: 'incoming_count', align: 'center' },
                          { 
                            header: '  (IQD)', 
                            field: 'earned_commission_iqd', 
                            align: 'center',
                            render: (val) => val?.toLocaleString() || '0'
                          },
                          { 
                            header: '  (IQD)', 
                            field: 'paid_commission_iqd', 
                            align: 'center',
                            render: (val) => val?.toLocaleString() || '0'
                          },
                          { 
                            header: '  (IQD)', 
                            field: 'net_commission_iqd', 
                            align: 'center',
                            bold: true,
                            render: (val) => val?.toLocaleString() || '0'
                          }
                        ]}
                      />
                    }
                    buttonText=""
                    fileName={`commissions-report-${selectedDate}.pdf`}
                  />
                )}
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Tabs */}
        {commissionsReport && (
          <>
            <div className="flex gap-2 border-b-2">
              <button
                onClick={() => setActiveTab('summary')}
                className={`px-6 py-3 font-bold text-lg transition-all ${
                  activeTab === 'summary'
                    ? 'border-b-4 border-primary text-primary bg-primary/5'
                    : 'text-muted-foreground hover:text-primary'
                }`}
              >
                  
              </button>
              <button
                onClick={() => setActiveTab('agents')}
                className={`px-6 py-3 font-bold text-lg transition-all ${
                  activeTab === 'agents'
                    ? 'border-b-4 border-primary text-primary bg-primary/5'
                    : 'text-muted-foreground hover:text-primary'
                }`}
              >
                  
              </button>
            </div>

            {/* Summary Tab */}
            {activeTab === 'summary' && (
              <div className="space-y-6">
                {/* Summary Cards */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {/* IQD Summary */}
                  <Card className="border-2 border-green-200 bg-green-50">
                    <CardHeader>
                      <CardTitle className="text-xl text-green-900">
                           (IQD)
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                      <div className="bg-white p-4 rounded-lg">
                        <p className="text-sm text-muted-foreground">  ()</p>
                        <p className="text-3xl font-bold text-green-700">
                          {formatCurrency(commissionsReport.summary.total_earned_iqd, 'IQD')}
                        </p>
                      </div>
                      <div className="bg-white p-4 rounded-lg">
                        <p className="text-sm text-muted-foreground">  ()</p>
                        <p className="text-3xl font-bold text-red-700">
                          {formatCurrency(commissionsReport.summary.total_paid_iqd, 'IQD')}
                        </p>
                      </div>
                      <div className="bg-gradient-to-br from-green-600 to-green-800 p-4 rounded-lg">
                        <p className="text-sm text-white opacity-90"> </p>
                        <p className="text-3xl font-bold text-white">
                          {formatCurrency(commissionsReport.summary.net_profit_iqd, 'IQD')}
                        </p>
                      </div>
                    </CardContent>
                  </Card>

                  {/* USD Summary */}
                  <Card className="border-2 border-blue-200 bg-blue-50">
                    <CardHeader>
                      <CardTitle className="text-xl text-blue-900">
                           (USD)
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                      <div className="bg-white p-4 rounded-lg">
                        <p className="text-sm text-muted-foreground">  ()</p>
                        <p className="text-3xl font-bold text-green-700">
                          {formatCurrency(commissionsReport.summary.total_earned_usd, 'USD')}
                        </p>
                      </div>
                      <div className="bg-white p-4 rounded-lg">
                        <p className="text-sm text-muted-foreground">  ()</p>
                        <p className="text-3xl font-bold text-red-700">
                          {formatCurrency(commissionsReport.summary.total_paid_usd, 'USD')}
                        </p>
                      </div>
                      <div className="bg-gradient-to-br from-blue-600 to-blue-800 p-4 rounded-lg">
                        <p className="text-sm text-white opacity-90"> </p>
                        <p className="text-3xl font-bold text-white">
                          {formatCurrency(commissionsReport.summary.net_profit_usd, 'USD')}
                        </p>
                      </div>
                    </CardContent>
                  </Card>
                </div>

                {/* Transactions Details */}
                <Card>
                  <CardHeader>
                    <CardTitle>  </CardTitle>
                    <CardDescription>
                      {commissionsReport.earned_commissions?.length || 0} 
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    {commissionsReport.earned_commissions?.length > 0 ? (
                      <div className="overflow-x-auto">
                        <table className="w-full text-sm">
                          <thead className="bg-gray-100">
                            <tr>
                              <th className="p-2 text-right"></th>
                              <th className="p-2 text-right"></th>
                              <th className="p-2 text-right"> </th>
                              <th className="p-2 text-right"></th>
                              <th className="p-2 text-right"></th>
                            </tr>
                          </thead>
                          <tbody>
                            {commissionsReport.earned_commissions.map((comm, idx) => (
                              <tr key={idx} className="border-t">
                                <td className="p-2">{new Date(comm.created_at).toLocaleDateString('ar-IQ')}</td>
                                <td className="p-2">{comm.agent_name}</td>
                                <td className="p-2">{comm.transfer_code}</td>
                                <td className="p-2 font-bold text-green-700">
                                  {formatCurrency(comm.amount, comm.currency)}
                                </td>
                                <td className="p-2">{comm.commission_percentage}%</td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>
                    ) : (
                      <p className="text-center py-4 text-muted-foreground">  </p>
                    )}
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader>
                    <CardTitle>  </CardTitle>
                    <CardDescription>
                      {commissionsReport.paid_commissions?.length || 0} 
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    {commissionsReport.paid_commissions?.length > 0 ? (
                      <div className="overflow-x-auto">
                        <table className="w-full text-sm">
                          <thead className="bg-gray-100">
                            <tr>
                              <th className="p-2 text-right"></th>
                              <th className="p-2 text-right"></th>
                              <th className="p-2 text-right"> </th>
                              <th className="p-2 text-right"></th>
                              <th className="p-2 text-right"></th>
                            </tr>
                          </thead>
                          <tbody>
                            {commissionsReport.paid_commissions.map((comm, idx) => (
                              <tr key={idx} className="border-t">
                                <td className="p-2">{new Date(comm.created_at).toLocaleDateString('ar-IQ')}</td>
                                <td className="p-2">{comm.agent_name}</td>
                                <td className="p-2">{comm.transfer_code}</td>
                                <td className="p-2 font-bold text-red-700">
                                  {formatCurrency(comm.amount, comm.currency)}
                                </td>
                                <td className="p-2">{comm.commission_percentage}%</td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>
                    ) : (
                      <p className="text-center py-4 text-muted-foreground">  </p>
                    )}
                  </CardContent>
                </Card>
              </div>
            )}

            {/* Agents Tab */}
            {activeTab === 'agents' && agentsReport && (
              <div className="space-y-4">
                {agentsReport.agents?.length > 0 ? (
                  agentsReport.agents.map((agent) => (
                    <Card key={agent.agent_id} className="border-2">
                      <CardHeader className="bg-gradient-to-l from-gray-50 to-white">
                        <CardTitle className="text-xl">{agent.agent_name}</CardTitle>
                      </CardHeader>
                      <CardContent className="pt-6">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                          {/* IQD */}
                          <div className="space-y-2">
                            <p className="font-bold text-green-900">  (IQD)</p>
                            <div className="grid grid-cols-3 gap-2 text-sm">
                              <div className="bg-green-50 p-2 rounded">
                                <p className="text-xs text-muted-foreground"></p>
                                <p className="font-bold text-green-700">
                                  {agent.IQD.earned.toLocaleString()}
                                </p>
                              </div>
                              <div className="bg-red-50 p-2 rounded">
                                <p className="text-xs text-muted-foreground"></p>
                                <p className="font-bold text-red-700">
                                  {agent.IQD.paid.toLocaleString()}
                                </p>
                              </div>
                              <div className="bg-purple-50 p-2 rounded">
                                <p className="text-xs text-muted-foreground"></p>
                                <p className="font-bold text-purple-700">
                                  {agent.IQD.net.toLocaleString()}
                                </p>
                              </div>
                            </div>
                          </div>

                          {/* USD */}
                          <div className="space-y-2">
                            <p className="font-bold text-blue-900">  (USD)</p>
                            <div className="grid grid-cols-3 gap-2 text-sm">
                              <div className="bg-green-50 p-2 rounded">
                                <p className="text-xs text-muted-foreground"></p>
                                <p className="font-bold text-green-700">
                                  {agent.USD.earned.toLocaleString()}
                                </p>
                              </div>
                              <div className="bg-red-50 p-2 rounded">
                                <p className="text-xs text-muted-foreground"></p>
                                <p className="font-bold text-red-700">
                                  {agent.USD.paid.toLocaleString()}
                                </p>
                              </div>
                              <div className="bg-purple-50 p-2 rounded">
                                <p className="text-xs text-muted-foreground"></p>
                                <p className="font-bold text-purple-700">
                                  {agent.USD.net.toLocaleString()}
                                </p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  ))
                ) : (
                  <Card>
                    <CardContent className="p-8 text-center text-muted-foreground">
                            
                    </CardContent>
                  </Card>
                )}
              </div>
            )}
          </>
        )}
      </div>
    </div>
  );
};

export default ReportsPage;
