//    
export const numberToArabic = (num) => {
  if (num === 0) return "";
  
  const ones = ["", "", "", "", "", "", "", "", "", ""];
  const tens = ["", "", "", "", "", "", "", "", "", ""];
  const hundreds = ["", "", "", "", "", "", "", "", "", ""];
  
  num = Math.floor(num);
  
  if (num < 0) return " " + numberToArabic(-num);
  
  if (num < 10) return ones[num];
  if (num < 20) {
    if (num === 10) return "";
    if (num === 11) return " ";
    if (num === 12) return " ";
    return ones[num - 10] + " ";
  }
  if (num < 100) {
    return tens[Math.floor(num / 10)] + (num % 10 !== 0 ? " " + ones[num % 10] : "");
  }
  if (num < 1000) {
    return hundreds[Math.floor(num / 100)] + (num % 100 !== 0 ? " " + numberToArabic(num % 100) : "");
  }
  if (num < 1000000) {
    const thousands = Math.floor(num / 1000);
    const remainder = num % 1000;
    let result;
    
    if (thousands === 1) result = "";
    else if (thousands === 2) result = "";
    else if (thousands <= 10) result = numberToArabic(thousands) + " ";
    else result = numberToArabic(thousands) + " ";
    
    if (remainder !== 0) result += " " + numberToArabic(remainder);
    return result;
  }
  if (num < 1000000000) {
    const millions = Math.floor(num / 1000000);
    const remainder = num % 1000000;
    let result;
    
    if (millions === 1) result = "";
    else if (millions === 2) result = "";
    else if (millions <= 10) result = numberToArabic(millions) + " ";
    else result = numberToArabic(millions) + " ";
    
    if (remainder !== 0) result += " " + numberToArabic(remainder);
    return result;
  }
  
  return num.toLocaleString();
};

export const formatWalletRequired = (balance, limit, currency) => {
  if (limit === 0) return { status: " ", number: null, text: null };
  
  const required = limit - balance;
  if (required <= 0) return { status: " ", number: null, text: null };
  
  const arabicNum = numberToArabic(required);
  const currencyName = currency === 'IQD' ? '' : '';
  
  return {
    status: null,
    number: required.toLocaleString(),
    text: `${arabicNum} ${currencyName}`
  };
};

//       
export const formatAmountInWords = (amount, currency = 'IQD') => {
  if (!amount || amount === 0) return "";
  
  const arabicNum = numberToArabic(Math.floor(amount));
  const currencyName = currency === 'IQD' ? ' ' : ' ';
  
  return `${arabicNum} ${currencyName}   `;
};
