import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { toast } from 'sonner';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Label } from '../components/ui/label';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '../components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from '../components/ui/dialog';
import { Checkbox } from '../components/ui/checkbox';
import { Pencil, Trash2, Plus, Shield } from 'lucide-react';
import api from '../services/api';


//   
const AVAILABLE_PERMISSIONS = [
  { id: 'dashboard', label: '', icon: '' },
  { id: 'transfers', label: '', icon: '' },
  { id: 'create_transfer', label: ' ', icon: '' },
  { id: 'visual_designer', label: ' ', icon: '' },
  { id: 'admin_dashboard', label: ' ', icon: '' },
  { id: 'agents', label: ' ', icon: '' },
  { id: 'wallet_manage', label: ' ', icon: '' },
  { id: 'notifications', label: '', icon: '' },
  { id: 'trial_balance', label: ' ', icon: '' },
  { id: 'chart_of_accounts', label: ' ', icon: '' },
  { id: 'ledger', label: ' ', icon: '' },
  { id: 'journal', label: ' ', icon: '' },
  { id: 'manual_journal', label: ' ', icon: '' },
  { id: 'journal_transfer', label: ' ', icon: '' },
  { id: 'reports', label: ' ', icon: '' },
  { id: 'journal_by_period', label: '   ', icon: '' },
  { id: 'users_management', label: ' ', icon: '' },
];

const UsersManagementPage = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showAddDialog, setShowAddDialog] = useState(false);
  const [showEditDialog, setShowEditDialog] = useState(false);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [selectedUser, setSelectedUser] = useState(null);
  const [formData, setFormData] = useState({
    username: '',
    display_name: '',
    email: '',
    password: '',
    permissions: []
  });

  useEffect(() => {
    if (user?.role !== 'admin') {
      toast.error('  ');
      navigate('/dashboard');
      return;
    }
    fetchUsers();
  }, [user, navigate]);

  const fetchUsers = async () => {
    setLoading(true);
    try {
      const response = await api.get('/admin/users');
      // Filter only admin users (not agents or regular users)
      const adminUsers = response.data.filter(u => u.role === 'admin' || u.role === 'admin_user');
      setUsers(adminUsers);
    } catch (error) {
      console.error('Error fetching users:', error);
      toast.error('   ');
    }
    setLoading(false);
  };

  const handleAddUser = async () => {
    if (!formData.username || !formData.display_name || !formData.password) {
      toast.error('    ');
      return;
    }

    setLoading(true);
    try {
      await api.post('/admin/users', {
        username: formData.username,
        display_name: formData.display_name,
        email: formData.email || null,
        password: formData.password,
        role: 'admin_user', //  
        permissions: formData.permissions
      });
      toast.success('   ');
      setShowAddDialog(false);
      setFormData({ username: '', display_name: '', email: '', password: '', permissions: [] });
      fetchUsers();
    } catch (error) {
      console.error('Error adding user:', error);
      toast.error(error.response?.data?.detail || '   ');
    }
    setLoading(false);
  };

  const handleEditUser = async () => {
    if (!selectedUser) return;

    setLoading(true);
    try {
      await api.put('/admin/users/${selectedUser.id}', {
        display_name: formData.display_name,
        email: formData.email || null,
        permissions: formData.permissions,
        ...(formData.password && { password: formData.password }) // Update password only if provided
      });
      toast.success('   ');
      setShowEditDialog(false);
      setSelectedUser(null);
      setFormData({ username: '', display_name: '', email: '', password: '', permissions: [] });
      fetchUsers();
    } catch (error) {
      console.error('Error updating user:', error);
      toast.error('   ');
    }
    setLoading(false);
  };

  const handleDeleteUser = async () => {
    if (!selectedUser) return;

    setLoading(true);
    try {
      await api.delete('/admin/users/${selectedUser.id}');
      toast.success('   ');
      setShowDeleteDialog(false);
      setSelectedUser(null);
      fetchUsers();
    } catch (error) {
      console.error('Error deleting user:', error);
      toast.error('   ');
    }
    setLoading(false);
  };

  const openEditDialog = (user) => {
    setSelectedUser(user);
    setFormData({
      username: user.username,
      display_name: user.display_name,
      email: user.email || '',
      password: '',
      permissions: user.permissions || []
    });
    setShowEditDialog(true);
  };

  const togglePermission = (permissionId) => {
    setFormData(prev => ({
      ...prev,
      permissions: prev.permissions.includes(permissionId)
        ? prev.permissions.filter(p => p !== permissionId)
        : [...prev.permissions, permissionId]
    }));
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container mx-auto p-4 sm:p-6 space-y-6">
        {/* Header */}
        <Card className="border-0 shadow-lg">
          <CardHeader className="bg-gradient-to-l from-blue-50 to-blue-100">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
              <div>
                <CardTitle className="text-2xl sm:text-3xl">  </CardTitle>
                <CardDescription className="text-base">
                      
                </CardDescription>
              </div>
              <Button onClick={() => setShowAddDialog(true)} className="w-full sm:w-auto">
                <Plus className="w-4 h-4 ml-2" />
                  
              </Button>
            </div>
          </CardHeader>
        </Card>

        {/* Users List */}
        <Card>
          <CardContent className="pt-6">
            {loading ? (
              <div className="text-center py-8"> ...</div>
            ) : users.length === 0 ? (
              <div className="text-center py-8 text-gray-500">  </div>
            ) : (
              <div className="overflow-x-auto">
                <table className="w-full text-sm">
                  <thead className="bg-gray-100">
                    <tr>
                      <th className="p-3 text-right"> </th>
                      <th className="p-3 text-right"> </th>
                      <th className="p-3 text-right"> </th>
                      <th className="p-3 text-center"> </th>
                      <th className="p-3 text-center"></th>
                    </tr>
                  </thead>
                  <tbody>
                    {users.map((u) => (
                      <tr key={u.id} className="border-t hover:bg-gray-50">
                        <td className="p-3 font-semibold">{u.username}</td>
                        <td className="p-3">{u.display_name}</td>
                        <td className="p-3 text-gray-600">{u.email || '-'}</td>
                        <td className="p-3 text-center">
                          <span className="bg-blue-100 text-blue-800 px-2 py-1 rounded text-xs">
                            {u.permissions?.length || 0} 
                          </span>
                        </td>
                        <td className="p-3 text-center">
                          <div className="flex gap-2 justify-center">
                            <Button
                              variant="outline"
                              size="sm"
                              onClick={() => openEditDialog(u)}
                            >
                              <Pencil className="w-4 h-4" />
                            </Button>
                            <Button
                              variant="outline"
                              size="sm"
                              onClick={() => {
                                setSelectedUser(u);
                                setShowDeleteDialog(true);
                              }}
                              className="text-red-600 hover:text-red-700"
                            >
                              <Trash2 className="w-4 h-4" />
                            </Button>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Add User Dialog */}
        <Dialog open={showAddDialog} onOpenChange={setShowAddDialog}>
          <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>  </DialogTitle>
              <DialogDescription>    </DialogDescription>
            </DialogHeader>

            <div className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label>  <span className="text-red-500">*</span></Label>
                  <Input
                    value={formData.username}
                    onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                    placeholder="username"
                  />
                </div>
                <div className="space-y-2">
                  <Label>  <span className="text-red-500">*</span></Label>
                  <Input
                    value={formData.display_name}
                    onChange={(e) => setFormData({ ...formData, display_name: e.target.value })}
                    placeholder=" "
                  />
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label> </Label>
                  <Input
                    type="email"
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    placeholder="email@example.com"
                  />
                </div>
                <div className="space-y-2">
                  <Label>  <span className="text-red-500">*</span></Label>
                  <Input
                    type="password"
                    value={formData.password}
                    onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                    placeholder="********"
                  />
                </div>
              </div>

              <div className="space-y-2">
                <Label className="flex items-center gap-2">
                  <Shield className="w-4 h-4" />
                  
                </Label>
                <div className="border rounded-lg p-4 max-h-60 overflow-y-auto">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                    {AVAILABLE_PERMISSIONS.map((perm) => (
                      <div key={perm.id} className="flex items-center space-x-2 space-x-reverse">
                        <Checkbox
                          id={`perm-${perm.id}`}
                          checked={formData.permissions.includes(perm.id)}
                          onCheckedChange={() => togglePermission(perm.id)}
                        />
                        <label
                          htmlFor={`perm-${perm.id}`}
                          className="text-sm font-medium leading-none cursor-pointer flex items-center gap-2"
                        >
                          <span>{perm.icon}</span>
                          <span>{perm.label}</span>
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            <DialogFooter>
              <Button variant="outline" onClick={() => setShowAddDialog(false)}>
                
              </Button>
              <Button onClick={handleAddUser} disabled={loading}>
                {loading ? ' ...' : ' '}
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>

        {/* Edit User Dialog */}
        <Dialog open={showEditDialog} onOpenChange={setShowEditDialog}>
          <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle> </DialogTitle>
              <DialogDescription>   </DialogDescription>
            </DialogHeader>

            <div className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label> </Label>
                  <Input value={formData.username} disabled />
                </div>
                <div className="space-y-2">
                  <Label>  <span className="text-red-500">*</span></Label>
                  <Input
                    value={formData.display_name}
                    onChange={(e) => setFormData({ ...formData, display_name: e.target.value })}
                  />
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label> </Label>
                  <Input
                    type="email"
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  />
                </div>
                <div className="space-y-2">
                  <Label>   ()</Label>
                  <Input
                    type="password"
                    value={formData.password}
                    onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                    placeholder="      "
                  />
                </div>
              </div>

              <div className="space-y-2">
                <Label className="flex items-center gap-2">
                  <Shield className="w-4 h-4" />
                  
                </Label>
                <div className="border rounded-lg p-4 max-h-60 overflow-y-auto">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                    {AVAILABLE_PERMISSIONS.map((perm) => (
                      <div key={perm.id} className="flex items-center space-x-2 space-x-reverse">
                        <Checkbox
                          id={`edit-perm-${perm.id}`}
                          checked={formData.permissions.includes(perm.id)}
                          onCheckedChange={() => togglePermission(perm.id)}
                        />
                        <label
                          htmlFor={`edit-perm-${perm.id}`}
                          className="text-sm font-medium leading-none cursor-pointer flex items-center gap-2"
                        >
                          <span>{perm.icon}</span>
                          <span>{perm.label}</span>
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            <DialogFooter>
              <Button variant="outline" onClick={() => setShowEditDialog(false)}>
                
              </Button>
              <Button onClick={handleEditUser} disabled={loading}>
                {loading ? ' ...' : ' '}
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>

        {/* Delete Confirmation Dialog */}
        <Dialog open={showDeleteDialog} onOpenChange={setShowDeleteDialog}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>  </DialogTitle>
              <DialogDescription>
                      "{selectedUser?.display_name}"
                <br />
                     .
              </DialogDescription>
            </DialogHeader>
            <DialogFooter>
              <Button variant="outline" onClick={() => setShowDeleteDialog(false)}>
                
              </Button>
              <Button variant="destructive" onClick={handleDeleteUser} disabled={loading}>
                {loading ? ' ...' : ' '}
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  );
};

export default UsersManagementPage;
