import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { Button } from '../components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '../components/ui/card';
import { toast } from 'sonner';
import api from '../services/api';


const NotificationsPage = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [notifications, setNotifications] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('all'); // all, unread

  useEffect(() => {
    fetchNotifications();
  }, [filter]);

  const fetchNotifications = async () => {
    setLoading(true);
    try {
      const response = await api.get('/notifications', {
        params: { unread_only: filter === 'unread' }
      });
      setNotifications(response.data.notifications || []);
    } catch (error) {
      console.error('Error fetching notifications:', error);
      toast.error('   ');
    }
    setLoading(false);
  };

  const markAsRead = async (notificationId) => {
    try {
      await api.patch('/notifications/${notificationId}/mark-read');
      fetchNotifications();
    } catch (error) {
      console.error('Error marking as read:', error);
    }
  };

  const getSeverityColor = (severity) => {
    switch (severity) {
      case 'critical':
        return 'border-red-500 bg-red-50';
      case 'high':
        return 'border-orange-500 bg-orange-50';
      case 'medium':
        return 'border-yellow-500 bg-yellow-50';
      default:
        return 'border-blue-500 bg-blue-50';
    }
  };

  const getSeverityIcon = (severity) => {
    switch (severity) {
      case 'critical':
        return '';
      case 'high':
        return '';
      case 'medium':
        return '';
      default:
        return '';
    }
  };
  
  const getNotificationTypeIcon = (type) => {
    const typeIcons = {
      'wallet_deposit': '',
      'new_transfer': '',
      'transfer_received': '',
      'duplicate_transfer': '',
      'name_mismatch': '',
      'id_verification_failed': '',
      'suspicious_activity': '',
      'ai_warning': '',
      'system': ''
    };
    return typeIcons[type] || '';
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-background">
        
        <div className="container mx-auto p-6 flex justify-center items-center">
          <div className="text-lg"> ...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#F5F7FA]">
      
      <div className="container mx-auto p-4 sm:p-6 space-y-6">
        {/* Header */}
        <Card className="border-0 shadow-lg">
          <CardHeader className="bg-gradient-to-l from-primary/10 to-primary/5">
            <CardTitle className="text-2xl sm:text-3xl"> </CardTitle>
            <CardDescription className="text-base">
                 
            </CardDescription>
          </CardHeader>
          <CardContent className="pt-6">
            <div className="flex gap-2">
              <Button
                variant={filter === 'all' ? 'default' : 'outline'}
                onClick={() => setFilter('all')}
              >
                 
              </Button>
              <Button
                variant={filter === 'unread' ? 'default' : 'outline'}
                onClick={() => setFilter('unread')}
              >
                 
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Notifications List */}
        {notifications.length === 0 ? (
          <Card>
            <CardContent className="p-8 text-center text-muted-foreground">
              {filter === 'unread' ? '    ' : '  '}
            </CardContent>
          </Card>
        ) : (
          <div className="space-y-4">
            {notifications.map((notification) => (
              <Card
                key={notification.id}
                className={`border-2 ${getSeverityColor(notification.severity)} ${
                  !notification.is_read ? 'shadow-lg' : ''
                }`}
              >
                <CardHeader className="pb-3">
                  <div className="flex justify-between items-start gap-4">
                    <div className="flex-1">
                      <div className="flex items-center gap-2 mb-2">
                        <span className="text-2xl">{getNotificationTypeIcon(notification.type)}</span>
                        <span className="text-xl">{getSeverityIcon(notification.severity)}</span>
                        <CardTitle className="text-lg">{notification.title}</CardTitle>
                        {!notification.is_read && (
                          <span className="px-2 py-1 bg-primary text-white text-xs rounded-full">
                            
                          </span>
                        )}
                      </div>
                      <CardDescription className="text-base whitespace-pre-line">
                        {notification.message}
                      </CardDescription>
                      
                      {/* AI Analysis Badge */}
                      {notification.ai_analysis && (
                        <div className="mt-3 p-3 bg-gradient-to-r from-purple-50 to-blue-50 border-2 border-purple-200 rounded-lg">
                          <div className="flex items-center gap-2 mb-1">
                            <span className="text-lg"></span>
                            <span className="font-semibold text-purple-700">  :</span>
                          </div>
                          <p className="text-sm text-gray-700">{notification.ai_analysis}</p>
                        </div>
                      )}
                    </div>
                    {!notification.is_read && (
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => markAsRead(notification.id)}
                      >
                          
                      </Button>
                    )}
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="flex flex-wrap gap-2 text-sm text-muted-foreground">
                    <span>
                       {new Date(notification.created_at).toLocaleString('ar-IQ')}
                    </span>
                    {notification.related_transfer_id && (
                      <Button
                        size="sm"
                        variant="link"
                        className="h-auto p-0"
                        onClick={() => navigate('/transfers/${notification.related_transfer_id}')}
                      >
                          
                      </Button>
                    )}
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default NotificationsPage;
