import React, { useState, useEffect, useRef } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import Webcam from 'react-webcam';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Label } from '../components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card';
import { Badge } from '../components/ui/badge';
import { toast } from 'sonner';
import { formatAmountInWords } from '../utils/arabicNumbers';
import { printDocument, generateTransferReceiptHTML, generateVoucherHTML, generateHTMLFromVisualTemplate } from '../utils/printUtils';
import api from '../services/api';


const TransferDetailsPage = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [transfer, setTransfer] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showReceive, setShowReceive] = useState(false);
  const [showPin, setShowPin] = useState(false);
  const [pinData, setPinData] = useState(null);
  const [loadingPin, setLoadingPin] = useState(false);
  
  // Edit and Cancel states
  const [showEdit, setShowEdit] = useState(false);
  const [editData, setEditData] = useState({
    sender_name: '',
    receiver_name: '',
    amount: '',
    note: ''
  });
  const [loadingCancel, setLoadingCancel] = useState(false);
  const [loadingEdit, setLoadingEdit] = useState(false);
  
  // Receive form states
  const [pin, setPin] = useState('');
  const [receiverFullname, setReceiverFullname] = useState('');
  const [useCamera, setUseCamera] = useState(false);
  const [facingMode, setFacingMode] = useState('environment'); // 'user' for front, 'environment' for back
  const [capturedImage, setCapturedImage] = useState(null);
  const [uploadedFile, setUploadedFile] = useState(null);
  const [submitting, setSubmitting] = useState(false);
  const webcamRef = useRef(null);
  
  // Commission calculation states
  const [expectedCommission, setExpectedCommission] = useState(null);
  const [loadingCommission, setLoadingCommission] = useState(false);

  useEffect(() => {
    fetchTransfer();
  }, [id]);
  
  // Calculate expected commission when receive form is shown
  useEffect(() => {
    if (showReceive && transfer && user) {
      fetchExpectedCommission();
    }
  }, [showReceive, transfer, user]);
  
  const fetchExpectedCommission = async () => {
    setLoadingCommission(true);
    try {
      const response = await api.get('/commission/calculate-preview', {
        params: {
          amount: transfer.amount,
          currency: transfer.currency,
          to_governorate: transfer.to_governorate
        }
      });
      setExpectedCommission(response.data);
    } catch (error) {
      console.error('Error calculating commission:', error);
      setExpectedCommission(null);
    } finally {
      setLoadingCommission(false);
    }
  };

  const fetchTransfer = async () => {
    try {
      const response = await api.get('/transfers/${id}');
      setTransfer(response.data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching transfer:', error);
      toast.error('    ');
      navigate('/transfers');
    }
  };

  const fetchPin = async () => {
    setLoadingPin(true);
    try {
      const response = await api.get('/transfers/${id}/pin');
      setPinData(response.data);
      setShowPin(true);
      toast.success('   ');
    } catch (error) {
      console.error('Error fetching PIN:', error);
      toast.error(error.response?.data?.detail || '    ');
    } finally {
      setLoadingPin(false);
    }
  };

  const handleCancelTransfer = async () => {
    if (!window.confirm('      ')) {
      return;
    }

    setLoadingCancel(true);
    try {
      await api.patch('/transfers/${id}/cancel');
      toast.success('   .    .');
      fetchTransfer(); // Refresh transfer data
    } catch (error) {
      console.error('Error cancelling transfer:', error);
      toast.error(error.response?.data?.detail || '   ');
    } finally {
      setLoadingCancel(false);
    }
  };

  const handleEditTransfer = async (e) => {
    e.preventDefault();
    setLoadingEdit(true);

    try {
      const updateData = {};
      if (editData.sender_name) updateData.sender_name = editData.sender_name;
      if (editData.receiver_name) updateData.receiver_name = editData.receiver_name;
      if (editData.amount) updateData.amount = parseFloat(editData.amount);
      if (editData.note) updateData.note = editData.note;

      await api.patch('/transfers/${id}/update', updateData);
      toast.success('   ');
      setShowEdit(false);
      fetchTransfer(); // Refresh transfer data
    } catch (error) {
      console.error('Error updating transfer:', error);
      toast.error(error.response?.data?.detail || '   ');
    } finally {
      setLoadingEdit(false);
    }
  };

  const captureImage = () => {
    if (webcamRef.current) {
      const imageSrc = webcamRef.current.getScreenshot();
      if (imageSrc) {
        setCapturedImage(imageSrc);
        setUseCamera(false);
      } else {
        toast.error('  .    .');
      }
    }
  };

  const switchCamera = () => {
    setFacingMode(prevMode => prevMode === 'user' ? 'environment' : 'user');
  };

  const handleCameraError = (error) => {
    console.error('Camera error:', error);
    toast.error('    .    .', {
      description: '        '
    });
    setUseCamera(false);
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        toast.error('       5MB');
        return;
      }
      setUploadedFile(file);
      setCapturedImage(null);
    }
  };

  const handleReceiveSubmit = async (e) => {
    e.preventDefault();
    
    if (!pin || !receiverFullname || (!capturedImage && !uploadedFile)) {
      toast.error('      ');
      return;
    }

    setSubmitting(true);

    try {
      const formData = new FormData();
      formData.append('pin', pin);
      formData.append('receiver_fullname', receiverFullname);

      if (capturedImage) {
        // Convert base64 to blob
        const blob = await fetch(capturedImage).then(r => r.blob());
        formData.append('id_image', blob, 'id_image.jpg');
      } else if (uploadedFile) {
        formData.append('id_image', uploadedFile);
      }

      await api.post('/transfers/${id}/receive', formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });

      toast.success('   !');
      navigate('/dashboard');
    } catch (error) {
      console.error('Error receiving transfer:', error);
      toast.error('', {
        description: error.response?.data?.detail || '  '
      });
    }

    setSubmitting(false);
  };

  const getStatusBadge = (status) => {
    const statusMap = {
      pending: { label: ' ', className: 'bg-yellow-100 text-yellow-800' },
      completed: { label: '', className: 'bg-green-100 text-green-800' },
      cancelled: { label: '', className: 'bg-red-100 text-red-800' }
    };
    const config = statusMap[status] || { label: status, className: '' };
    return <Badge className={config.className}>{config.label}</Badge>;
  };

  if (loading || !transfer) {
    return (
      <div className="min-h-screen bg-background">
        
        <div className="container mx-auto p-6 flex items-center justify-center min-h-[50vh]">
          <div className="text-2xl text-primary"> ...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background" data-testid="transfer-details-page">
      
      <div className="container mx-auto p-6 max-w-4xl">
        <Card className="shadow-2xl">
          <CardHeader className="bg-gradient-to-l from-primary/10 to-primary/5 py-6">
            <div className="flex items-start justify-between">
              {/* Right Side -     */}
              <div>
                <div className="mb-2">
                  <Label className="text-xs text-muted-foreground"> </Label>
                  <p className="text-2xl font-bold text-blue-600" data-testid="tracking-number">
                    {transfer.tracking_number || transfer.transfer_number || transfer.transfer_code}
                  </p>
                </div>
                <div>
                  <Label className="text-xs text-muted-foreground"> </Label>
                  <p className="text-xl font-bold text-primary" data-testid="transfer-code">
                    {transfer.transfer_code}
                  </p>
                </div>
              </div>

              {/* Center -   */}
              <div className="text-center">
                <CardTitle className="text-3xl text-primary"> </CardTitle>
              </div>

              {/* Left Side -   */}
              <div>
                {getStatusBadge(transfer.status)}
              </div>
            </div>
          </CardHeader>

          <CardContent className="pt-6 space-y-6">
            {/* Transfer Details */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label className="text-muted-foreground"> </Label>
                <p className="text-xl font-bold" data-testid="sender-name">{transfer.sender_name}</p>
              </div>
              <div className="space-y-2">
                <Label className="text-muted-foreground"> </Label>
                <p className="text-xl font-bold text-primary" data-testid="receiver-name">
                  {transfer.receiver_name || ' '}
                </p>
              </div>
              <div className="space-y-2">
                <Label className="text-muted-foreground"></Label>
                <p className="text-3xl font-bold text-secondary" data-testid="amount">
                  {transfer.amount.toLocaleString()} {transfer.currency || 'IQD'}
                </p>
                <p className="text-sm text-gray-600 italic bg-gray-50 p-2 rounded border border-gray-200">
                   {formatAmountInWords(transfer.amount, transfer.currency || 'IQD')}
                </p>
              </div>
              <div className="space-y-2">
                <Label className="text-muted-foreground"> </Label>
                <p className="text-lg font-bold">{transfer.from_agent_name || ' '}</p>
              </div>
              <div className="space-y-2">
                <Label className="text-muted-foreground"> </Label>
                <p className="text-lg font-bold">{transfer.to_governorate}</p>
              </div>
              {transfer.to_agent_name && (
                <div className="space-y-2">
                  <Label className="text-muted-foreground"> </Label>
                  <p className="text-lg font-bold">{transfer.to_agent_name}</p>
                </div>
              )}
              <div className="space-y-2">
                <Label className="text-muted-foreground"> </Label>
                <p className="text-sm">
                  {new Date(transfer.created_at).toLocaleDateString('ar-IQ', {
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric',
                    hour: '2-digit',
                    minute: '2-digit'
                  })}
                </p>
              </div>
              {transfer.note && (
                <div className="space-y-2 md:col-span-2">
                  <Label className="text-muted-foreground"></Label>
                  <p>{transfer.note}</p>
                </div>
              )}
            </div>

            {/* Commission Information -     */}
            {transfer.incoming_commission > 0 && (
              <div className="bg-gradient-to-r from-red-50 to-red-100 border-2 border-red-400 rounded-lg p-6">
                <h3 className="text-lg font-bold text-red-900 mb-4">   ()</h3>
                <div className="bg-white p-6 rounded-lg border-2 border-red-300 shadow-md">
                  <div className="flex items-center justify-between mb-4">
                    <Label className="text-base font-bold text-red-800">  </Label>
                    <span className="bg-red-200 text-red-900 px-3 py-1 rounded-full text-sm font-bold">
                      {transfer.incoming_commission_percentage || 0}%
                    </span>
                  </div>
                  <p className="text-4xl font-bold text-red-600 mb-4">
                    {transfer.incoming_commission?.toLocaleString()} {transfer.currency}
                  </p>
                  <div className="bg-red-50 border-2 border-red-300 rounded-lg p-4">
                    <p className="text-sm text-red-900 font-semibold mb-2">
                       <strong> :</strong>
                    </p>
                    <ul className="text-xs text-red-800 space-y-1 mr-4">
                      <li>       "  (5110)"</li>
                      <li>     </li>
                      <li>      (COM-PAID-{transfer.transfer_code})</li>
                    </ul>
                  </div>
                </div>
              </div>
            )}

            {/* Show PIN Button for Sender */}
            {user && transfer.from_agent_id === user.id && !showPin && (
              <div className="bg-yellow-50 border-2 border-yellow-300 rounded-lg p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm font-semibold text-yellow-800">
                         
                    </p>
                    <p className="text-xs text-yellow-700">
                            
                    </p>
                  </div>
                  <Button
                    onClick={fetchPin}
                    disabled={loadingPin}
                    className="bg-yellow-500 hover:bg-yellow-600 text-white"
                  >
                    {loadingPin ? ' ...' : '   '}
                  </Button>
                </div>
              </div>
            )}

            {/* PIN Display */}
            {showPin && pinData && (
              <Card className="border-4 border-secondary bg-gradient-to-r from-secondary/10 to-secondary/5">
                <CardHeader>
                  <CardTitle className="text-2xl text-secondary">  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label className="text-sm text-muted-foreground"> </Label>
                      <p className="text-2xl font-bold text-primary">{pinData.transfer_code}</p>
                    </div>
                    <div className="space-y-2">
                      <Label className="text-sm text-muted-foreground"> </Label>
                      <p className="text-4xl font-bold text-secondary">{pinData.pin}</p>
                    </div>
                    <div className="space-y-2">
                      <Label className="text-sm text-muted-foreground"> </Label>
                      <p className="text-lg font-bold">{pinData.receiver_name}</p>
                    </div>
                    <div className="space-y-2">
                      <Label className="text-sm text-muted-foreground"></Label>
                      <p className="text-lg font-bold">{pinData.amount.toLocaleString()} {pinData.currency}</p>
                      <p className="text-xs text-gray-600 italic bg-gray-50 p-2 rounded">
                         {formatAmountInWords(pinData.amount, pinData.currency)}
                      </p>
                    </div>
                  </div>
                  <div className="bg-yellow-50 border border-yellow-300 rounded p-3 text-sm text-yellow-800">
                     <strong>:</strong>           
                  </div>
                  <Button
                    onClick={() => setShowPin(false)}
                    variant="outline"
                    className="w-full"
                  >
                      
                  </Button>
                </CardContent>
              </Card>
            )}

            {/* Actions */}
            {transfer.status === 'pending' && !showReceive && user && (transfer.from_agent_id === user.id || user.role === 'admin') && (
              <div className="space-y-4">
                {/* Edit and Cancel Buttons for Sender or Admin */}
                <div className="bg-yellow-50 border-2 border-yellow-300 rounded-lg p-4">
                  <p className="text-sm font-semibold text-yellow-800 mb-3">
                    {user.role === 'admin' 
                      ? '   -    ' 
                      : '     -    '}
                  </p>
                  <div className="flex gap-3">
                    <Button
                      onClick={() => {
                        setEditData({
                          sender_name: transfer.sender_name,
                          receiver_name: transfer.receiver_name,
                          amount: transfer.amount,
                          note: transfer.note || ''
                        });
                        setShowEdit(true);
                      }}
                      className="flex-1 bg-blue-600 hover:bg-blue-700 text-white"
                    >
                        
                    </Button>
                    <Button
                      onClick={handleCancelTransfer}
                      disabled={loadingCancel}
                      className="flex-1 bg-red-600 hover:bg-red-700 text-white"
                    >
                      {loadingCancel ? ' ...' : '  '}
                    </Button>
                  </div>
                </div>
              </div>
            )}

            {/* Bottom Buttons */}
            <div className="flex gap-4 pt-6 border-t-2 border-gray-200 mt-6">
              {transfer.status === 'pending' && !showReceive && user && transfer.from_agent_id !== user.id && (
                <Button
                  onClick={() => setShowReceive(true)}
                  className="flex-1 bg-green-600 hover:bg-green-700 text-white text-lg font-bold py-6"
                  data-testid="receive-transfer-btn"
                >
                    
                </Button>
              )}
              
              {transfer.status === 'completed' && (
                <Button
                  onClick={async () => {
                    // Get PIN if not available
                    let transferWithPin = {...transfer};
                    if (!transfer.decrypted_pin) {
                      try {
                        const response = await api.get('/transfers/${transfer.id}/pin', {
                          headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
                        });
                        transferWithPin.decrypted_pin = response.data.pin;
                      } catch (error) {
                        console.error('Error fetching PIN:', error);
                      }
                    }
                    //    
                    let html;
                    try {
                      const templateType = transfer.status === 'completed' ? 'receive_transfer' : 'send_transfer';
                      const templateResponse = await api.get('/visual-templates/active/${templateType}', {
                        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
                      });
                      
                      if (templateResponse.data) {
                        html = generateHTMLFromVisualTemplate(templateResponse.data, transferWithPin);
                      } else {
                        throw new Error('No active template');
                      }
                    } catch (error) {
                      //    
                      const { generateVoucherHTML } = await import('../utils/printUtils');
                      html = generateVoucherHTML(transferWithPin);
                    }
                    
                    printDocument(html, `  ${transfer.transfer_number || transfer.transfer_code}`);
                  }}
                  className="flex-1 bg-blue-600 hover:bg-blue-700 text-white text-lg font-bold py-6"
                >
                    
                </Button>
              )}
              
              <Button
                onClick={() => navigate('/transfers')}
                variant="outline"
                className="flex-1 border-2 text-lg font-bold py-6"
                data-testid="back-to-list-btn"
              >
                 
              </Button>
            </div>

            {/* Edit Form */}
            {showEdit && transfer.status === 'pending' && (
              <Card className="border-2 border-blue-500">
                <CardHeader className="bg-blue-50">
                  <CardTitle className="text-xl text-blue-900">  </CardTitle>
                </CardHeader>
                <CardContent className="p-6">
                  <form onSubmit={handleEditTransfer} className="space-y-4">
                    <div className="space-y-2">
                      <Label> </Label>
                      <Input
                        value={editData.sender_name}
                        onChange={(e) => setEditData({...editData, sender_name: e.target.value})}
                        placeholder="  "
                      />
                    </div>
                    <div className="space-y-2">
                      <Label> </Label>
                      <Input
                        value={editData.receiver_name}
                        onChange={(e) => setEditData({...editData, receiver_name: e.target.value})}
                        placeholder="  "
                      />
                    </div>
                    <div className="space-y-2">
                      <Label></Label>
                      <Input
                        type="number"
                        value={editData.amount}
                        onChange={(e) => setEditData({...editData, amount: e.target.value})}
                        placeholder=""
                      />
                      <p className="text-xs text-yellow-700">
                              
                      </p>
                    </div>
                    <div className="space-y-2">
                      <Label></Label>
                      <Input
                        value={editData.note}
                        onChange={(e) => setEditData({...editData, note: e.target.value})}
                        placeholder=""
                      />
                    </div>
                    <div className="flex gap-3 pt-4">
                      <Button
                        type="submit"
                        disabled={loadingEdit}
                        className="flex-1 bg-blue-600 hover:bg-blue-700"
                      >
                        {loadingEdit ? ' ...' : '  '}
                      </Button>
                      <Button
                        type="button"
                        variant="outline"
                        onClick={() => setShowEdit(false)}
                        className="flex-1"
                      >
                        
                      </Button>
                    </div>
                  </form>
                </CardContent>
              </Card>
            )}

            {/* Receive Form */}
            {showReceive && transfer.status === 'pending' && (
              <Card className="border-2 border-secondary" data-testid="receive-form">
                <CardHeader className="bg-secondary/10">
                  <CardTitle className="text-2xl text-primary"> </CardTitle>
                  <CardDescription>  PIN  </CardDescription>
                </CardHeader>
                <CardContent className="pt-6">
                  <form onSubmit={handleReceiveSubmit} className="space-y-6">
                    <div className="space-y-2">
                      <Label htmlFor="pin" className="text-base font-bold">PIN (4 ) *</Label>
                      <Input
                        id="pin"
                        data-testid="pin-input"
                        type="password"
                        value={pin}
                        onChange={(e) => setPin(e.target.value)}
                        maxLength={6}
                        className="text-2xl tracking-widest h-14 text-center font-bold"
                        placeholder=" PIN"
                        required
                      />
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor="fullname" className="text-base font-bold">   *</Label>
                      <Input
                        id="fullname"
                        data-testid="fullname-input"
                        value={receiverFullname}
                        onChange={(e) => setReceiverFullname(e.target.value)}
                        className="text-base h-12"
                        placeholder="  "
                        required
                      />
                    </div>

                    <div className="space-y-4">
                      <Label className="text-base font-bold">     () *</Label>
                      <p className="text-sm text-gray-600">
                                
                      </p>
                      
                      <div className="flex gap-4">
                        <Button
                          type="button"
                          onClick={() => {
                            setCapturedImage(null);
                            setUseCamera(true);
                          }}
                          variant="outline"
                          className="flex-1 h-12"
                          data-testid="camera-btn"
                        >
                            
                        </Button>
                        <Button
                          type="button"
                          onClick={() => document.getElementById('file-upload').click()}
                          variant="outline"
                          className="flex-1 h-12"
                          data-testid="upload-btn"
                        >
                            
                        </Button>
                        <input
                          id="file-upload"
                          type="file"
                          accept="image/jpeg,image/png,image/jpg"
                          onChange={handleFileUpload}
                          className="hidden"
                        />
                      </div>

                      {/* Camera Full Screen Modal */}
                      {useCamera && (
                        <div className="fixed inset-0 z-[9999] bg-black">
                          {/* Header */}
                          <div className="absolute top-0 left-0 right-0 z-10 bg-gradient-to-b from-black via-black/80 to-transparent p-4 safe-area-top">
                            <div className="flex items-center justify-between text-white">
                              <h3 className="text-lg font-bold">   </h3>
                              <Button
                                type="button"
                                variant="ghost"
                                size="sm"
                                onClick={() => setUseCamera(false)}
                                className="text-white hover:bg-white/20 text-2xl px-4"
                              >
                                
                              </Button>
                            </div>
                          </div>

                          {/* Camera View */}
                          <div className="w-full h-full flex items-center justify-center">
                            <Webcam
                              ref={webcamRef}
                              audio={false}
                              screenshotFormat="image/jpeg"
                              screenshotQuality={0.95}
                              videoConstraints={{
                                facingMode: facingMode,
                                width: { min: 640, ideal: 1280, max: 1920 },
                                height: { min: 480, ideal: 720, max: 1080 },
                              }}
                              onUserMediaError={handleCameraError}
                              style={{
                                width: '100%',
                                height: '100%',
                                objectFit: 'cover',
                              }}
                            />
                          </div>

                          {/* Controls */}
                          <div className="absolute bottom-0 left-0 right-0 z-10 bg-gradient-to-t from-black via-black/80 to-transparent p-6 pb-8 safe-area-bottom">
                            <div className="flex items-center justify-center gap-6 mb-4">
                              {/* Switch Camera Button */}
                              <Button
                                type="button"
                                onClick={switchCamera}
                                size="lg"
                                className="h-16 w-16 rounded-full bg-white/30 text-white border-2 border-white hover:bg-white/40 backdrop-blur-sm flex items-center justify-center"
                              >
                                <span className="text-2xl"></span>
                              </Button>

                              {/* Capture Button */}
                              <Button
                                type="button"
                                onClick={captureImage}
                                size="lg"
                                className="h-20 w-20 rounded-full bg-white hover:bg-gray-200 border-4 border-secondary shadow-2xl flex items-center justify-center"
                                data-testid="capture-btn"
                              >
                                <span className="text-3xl"></span>
                              </Button>

                              {/* Gallery Button */}
                              <Button
                                type="button"
                                onClick={() => {
                                  setUseCamera(false);
                                  document.getElementById('file-upload').click();
                                }}
                                size="lg"
                                className="h-16 w-16 rounded-full bg-white/30 text-white border-2 border-white hover:bg-white/40 backdrop-blur-sm flex items-center justify-center"
                              >
                                <span className="text-2xl"></span>
                              </Button>
                            </div>
                            
                            <p className="text-center text-white text-base font-bold drop-shadow-lg">
                              {facingMode === 'user' ? '  ' : '  '}
                            </p>
                            <p className="text-center text-white/70 text-sm mt-2">
                                    
                            </p>
                          </div>
                        </div>
                      )}

                      {capturedImage && (
                        <div className="space-y-2">
                          <p className="text-sm text-green-600 font-bold">   </p>
                          <img src={capturedImage} alt="Captured" className="w-full rounded-lg border-2 border-green-500" />
                          <Button
                            type="button"
                            onClick={() => setCapturedImage(null)}
                            variant="outline"
                            size="sm"
                            className="w-full"
                          >
                                
                          </Button>
                        </div>
                      )}

                      {uploadedFile && (
                        <p className="text-sm text-green-600 font-bold">   : {uploadedFile.name}</p>
                      )}
                    </div>

                    {/* Expected Commission Display */}
                    {expectedCommission && expectedCommission.commission_amount > 0 && (
                      <div className="bg-gradient-to-r from-green-50 to-green-100 border-2 border-green-400 rounded-lg p-6">
                        <h4 className="text-lg font-bold text-green-900 mb-4 flex items-center gap-2">
                          <span></span>
                          <span>  -    </span>
                        </h4>
                        
                        <div className="bg-white rounded-lg p-4 border-2 border-green-300 shadow-sm">
                          <div className="grid grid-cols-2 gap-4 mb-4">
                            <div className="text-center p-3 bg-green-50 rounded-lg">
                              <p className="text-xs text-green-700 mb-1"> </p>
                              <p className="text-3xl font-bold text-green-600">
                                {expectedCommission.commission_percentage}%
                              </p>
                            </div>
                            <div className="text-center p-3 bg-green-50 rounded-lg">
                              <p className="text-xs text-green-700 mb-1">  ()</p>
                              <p className="text-3xl font-bold text-green-600">
                                {expectedCommission.commission_amount?.toLocaleString()}
                              </p>
                              <p className="text-xs text-green-700">{transfer.currency}</p>
                            </div>
                          </div>
                          
                          <div className="bg-blue-50 border border-blue-300 rounded-lg p-3">
                            <p className="text-sm text-blue-900 font-semibold mb-2">
                                :
                            </p>
                            <div className="space-y-1 text-sm">
                              <div className="flex justify-between">
                                <span className="text-gray-700"> :</span>
                                <span className="font-bold">{transfer.amount?.toLocaleString()} {transfer.currency}</span>
                              </div>
                              <div className="flex justify-between text-green-700">
                                <span className="font-semibold">+ :</span>
                                <span className="font-bold">+{expectedCommission.commission_amount?.toLocaleString()} {transfer.currency}</span>
                              </div>
                              <div className="border-t-2 border-gray-300 pt-1 mt-2"></div>
                              <div className="flex justify-between text-lg font-bold text-primary">
                                <span>  :</span>
                                <span>{(transfer.amount + expectedCommission.commission_amount)?.toLocaleString()} {transfer.currency}</span>
                              </div>
                            </div>
                          </div>
                          
                          <div className="mt-3 bg-yellow-50 border border-yellow-300 rounded p-2">
                            <p className="text-xs text-yellow-800">
                               <strong>:</strong>     +      
                            </p>
                          </div>
                        </div>
                      </div>
                    )}
                    
                    {loadingCommission && (
                      <div className="text-center py-4 text-gray-600">
                        <p>   ...</p>
                      </div>
                    )}

                    <div className="flex gap-4 pt-4">
                      <Button
                        type="button"
                        onClick={() => setShowReceive(false)}
                        variant="outline"
                        className="flex-1 border-2"
                        data-testid="cancel-receive-btn"
                      >
                        
                      </Button>
                      <Button
                        type="submit"
                        disabled={submitting}
                        className="flex-1 bg-secondary hover:bg-secondary/90 text-primary font-bold text-lg"
                        data-testid="submit-receive-btn"
                      >
                        {submitting ? ' ...' : ' '}
                      </Button>
                    </div>
                  </form>
                </CardContent>
              </Card>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default TransferDetailsPage;